.output {

  &__form {
    padding: 15px 15px 23px 15px;
    border-radius: 14px;
    background-image: linear-gradient(to right top, #2E2482, #4420E2);

    & > *:not(:last-child) {
      margin-bottom: 10px;
    }
  }

  &-bottom {
    display: flex;
    align-items: center;
    margin-top: 20px;
    padding-right: 5px;
  }

  &-text {
    text-align: right;
    font-size: 12rem;
    color: var(--color-light);
  }
}
